import divider from '@/components/divider.vue';
import uniIcon from '@/components/uni-icon/uni-icon.vue'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'

//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';

export default {
	components: {
		divider,
		uniIcon
	},
	data() {
		return {
			background: {
				backgroundColor: '#fff',
			},
			showSelect: false,
			params: {
				year: true,
				month: true,
				day: true,
				hour: true,
				minute: true,
				second: false
			},
			chooseIndex: -1,
			chooseDateIndex: -1,
			startTime: "选择开始时间",
			endTime: "选择结束时间",
			selectType: "选择指定条件",
			show: false,
			num: 1,
			catagor: [{
					name: '优惠买单',
					id: 1
				}, {
					name: '商城订单',
					id: 2
				}
			],
			now:"",
			nowDayOfWeek:"",
			nowDay:"",
			nowMonth:"",
			nowYear:"",
			type:"",
			page:1,
			reload: true,
			loadMore: false,
			status: 'more',
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			dataList:[],
			nodata:false
		}
	},
	
	onReachBottom() {
			this.loadMore = true;
			this.page++;  
			console.log(this.status)
			if (this.status == 'noMore') {
				return;
			}
			this.status = 'loading';
		    this.searchOrder(false)
	},
	

	methods: {
		selectTime(index) {
			this.show = true
			if (index == 1) {
				this.num = 1
			} else {
				this.num = 2
			}
		},
		// 选择时间
		chooseTime(res) {
			if (this.num == 1) {
				this.startTime = res.year + "-" + res.month + "-" + res.day + " " + res.hour + ":" + res.minute ;
			} else {
				this.endTime = res.year + "-" + res.month + "-" + res.day + " " + res.hour + ":" + res.minute ;
			}

		},

		// 显示条件弹窗
		selectFilter() {
			this.showSelect = true
		},

		// 选择条件
		chooseItem(num) {
			this.chooseIndex = num;
			this.type=num+1
			console.log(this.type)
		},


		cancle() {
			this.showSelect = false
		},

		confirm() {
			this.showSelect = false
			this.selectType = this.catagor[this.chooseIndex].name
		},


		chooseDate(index) {
			this.chooseDateIndex = index
			if(index==1){
				const end = new Date();
				const start =new Date(end.getTime() - 24*60*60*1000);
				this.startTime=this.formatDate(start)
				this.endTime=this.formatDate(end)
			}
			else if(index==2){
				const end = new Date();
				const start = new Date(new Date() - (new Date().getDay() - 1) * 86400000);
				this.startTime=this.formatDate(start)
				this.endTime=this.formatDate(end)
				
			}
			else{
				 var nowMonth = new Date().getMonth(); //当前月 
				 var nowYear = new Date().getFullYear(); //当前年 
				 const end = new Date();            
				 const start = new Date(nowYear, nowMonth, 1); 
				 this.startTime=this.formatDate(start)
				 this.endTime=this.formatDate(end)
			}
			
		},
		
		
		// 格式化日期
		formatDate(inputTime) {
		        var date = new Date(inputTime);
		        var y = date.getFullYear();
		        var m = date.getMonth() + 1;
		        m = m < 10 ? ('0' + m) : m;
		        var d = date.getDate();
		        d = d < 10 ? ('0' + d) : d;
		        var h = date.getHours();
		        h = h < 10 ? ('0' + h) : h;
		        var minute = date.getMinutes();
		        var second = date.getSeconds();
		        minute = minute < 10 ? ('0' + minute) : minute;
		        second = second < 10 ? ('0' + second) : second;
		        return y + '-' + m + '-' + d+' '+h+':'+minute;
		      },



		searchOrder(search) {
			if (this.endTime.indexOf("选择") && this.startTime.indexOf("选择")) {
				
				if(search){
					this.reload=true
					this.page=1
					this.status = 'more';
				}
				else{
					this.reload=false
				}
				
				let data={
					type:this.type||"",
					searchStartTime:this.startTime,
					searchEndTime:this.endTime,
					size:10,
					current:this.page,
				}
				
				// 发送到服务端
				$H.get("/order/page/all",data).then((res) => {
					//请求成功
					let list = res.data.records
					
					let pages=res.data.pages;
					
					console.log(this.page)
					if(pages>1&&this.page<=pages){
						this.dataList = this.reload ? list : this.dataList.concat(list);
					}
					else if(pages==0){
						this.dataList=[];
						this.nodata=true;
						this.status ='noMore'
					}
					else if(pages==1){
						this.dataList = list
						this.status ='noMore'
					}
					else{
						this.status ='noMore'
					}
					
					this.reload = false;
					
				}).catch((e) => {
					
					//请求失败
					console.log("失败"+e)
				})

			} else {
				console.log("不能")

			}
		}

	}
}
